<template>
  <div id="order">
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
         <el-card class="box-card">
            <el-row :gutter="30">
                <el-col :span="8">
                    <el-input placeholder="" v-model="queryInfo.query" @change="getOrderList" clearable>
                        <el-button @click='getOrderList' slot="append" icon="el-icon-search"></el-button>
                    </el-input>
                </el-col>
            </el-row>

            <el-table :data="orderList" border stripe>
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column prop="order_number" label=""></el-table-column>
                <el-table-column label="" width="120" align='center'>
                    <template slot-scope="scope">
                        {{''+scope.row.order_price}}                
                    </template>
                </el-table-column>
                <el-table-column  label=""  width="120"  align='center'>
                    <template slot-scope="scope">
                        <el-tag type="danger" v-if="scope.row.pay_status==0"></el-tag>  
                        <el-tag type="success" v-else></el-tag>           
                    </template>
                </el-table-column>
               <el-table-column prop="is_send" label="" width="120"  align='center'></el-table-column>
                <el-table-column label=""  width="200"  align='center'>
                     <template slot-scope="scope">
                        {{scope.row.create_time | dataFormat}}                
                    </template>
                </el-table-column>
                <el-table-column label=""  width="260"  align='center'>
                    <template slot-scope="scope">
                        <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row)"></el-button>
                        <el-button type="success" icon="el-icon-location" size="mini" @click="showProgressBox(scope.row)"></el-button>                
                    </template>
                </el-table-column>
            </el-table>

             <!--  -->
            <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryInfo.pagenum"
            :page-size="queryInfo.pagesize"
            :page-sizes="[6, 10, 15, 20]"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total"
             background>
            </el-pagination>
         </el-card>

         <!--  -->
         <el-dialog
        title=""
        :visible.sync="addressDialogVisible"
        width="50%"
        @close="addressDialogClosed('addressFormRef')">
        <el-form :model="addressForm" :rules="addressFormRules" ref="addressFormRef" label-width="100px">
            <el-form-item label="/" prop="address1">
                <el-cascader
                v-model="addressForm.address1"
                :options="cityData"
                expandTrigger='hover'
                clearable>
                </el-cascader>
            </el-form-item>
            <el-form-item label="" prop="address2">
                <el-input v-model.number="addressForm.address2" clearable></el-input>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="addressDialogVisible = false"> </el-button>
            <el-button type="primary" @click="addressDialogVisible = false"> </el-button>
        </span>
        </el-dialog>

        <!--  -->
         <el-dialog
          title=""
          :visible.sync="progressDialogVisible"
          width="50%">
          <el-timeline>
            <el-timeline-item
            v-for="(activity, index) in progressInfo"
            :key="index"
            type="success"
            color="green"
            :timestamp="activity.time">
            {{activity.context}}
           </el-timeline-item>
          </el-timeline>
        </el-dialog>
  </div>
</template>

<script>
import cityData from './citydata.js'
export default {
  name: "order",
  data() {
    return {
      queryInfo: {
        query: '', // 
        pagesize: 10, // 
        pagenum: 1, // 
      },
      // 
      orderList: [],
      // 
      total: 0,
      // 
      addressDialogVisible: false,
      // 
      progressDialogVisible: false,
      addressForm: {
        address1: [],
        address2: ''
      },
      addressFormRules:{
        address1: [
            {required: true,message:"/",trigger:'blur'},
          ],
        address2: [
            {required: true,message:"",trigger:'blur'},
        ]
      },
      // 
      cityData: cityData,
      // 
      progressInfo: [],

    }
  },
  methods: {
    // 
    async getOrderList(){
        const {data:res} = await this.$http.get('orders',{params: this.queryInfo})
        if(res.meta.status!==200){
          return this.$message.error('');
        }else{
          console.log(res.data);
          this.orderList = res.data.goods;
          this.total = res.data.total;
        }
    },
    //   pagesize 
      handleSizeChange(newSize){
        console.log(newSize)
        this.queryInfo.pagesize = newSize;
         this.getOrderList()
      },
      // 
      handleCurrentChange(newPage){
          console.log(newPage)
          this.queryInfo.pagenum = newPage;
           this.getOrderList()
      },
      // 
      showEditDialog(data){
          console.log(data);
          this.addressDialogVisible = true
      },
      // 
      async showProgressBox(data){
        console.log(data);
        //   
        const {data: res} = await this.$http.get('/kuaidi/773051686341128')
        if(res.meta.status!==200){
          return this.$message.error('')
        }else{
          this.progressInfo =res.data
          console.log(res.data)
        }
        this.progressDialogVisible = true
      },
      // 
      addressDialogClosed(formName){
           this.$refs[formName].resetFields();
      }
  },
  created() {
    this.getOrderList();
  },
};
</script>

<style lang="less" sceped>

</style>
